import React from "react";
import { Media, Row, ListGroup, ListGroupItem, Button, Card } from "reactstrap";
import { Link } from "react-router-dom";

// Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashAlt, faNewspaper, faMapMarker } from "@fortawesome/free-solid-svg-icons";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css

import axios from "axios";
const JobItemApplied = (props) => {
  const { firstColmn, secondColmn, textPositon } = props;
  const token = localStorage.getItem('token');
  const submit = (jobID) => {


    confirmAlert({
      title: 'Confirm to delete project',
      message: 'Are you sure to delete the project from your applied projects ?.',
      buttons: [
        {

          // label: <a href="/candidate/manage" style={{ color: 'white' }}> Yes </a>,
          label: 'Yes',
          onClick: () => {
            axios.delete(`https://www.togedoortestgo.site/graphql/accounts/${localStorage.id}/${jobID}`)
              .then((res) => {
                props.userAppliedProject(res.data)
                console.log(res);
              }).catch((err) => {
                console.log(err);
              })
          }
        },
        {
          label: 'No',
          // onClick: () => alert('Click No')
        }
      ]
    })
  };

  return (
    <Card className='p-5 d-p-sm-0 mt-5 rounded-0 job-item'>
      <Row className='pl-2'>
        <div className={`${firstColmn}`}>
          <Media className='p-2'>
            <Media left className='text-center'>
              <Media className='d-block' style={{ height: "100px", width: "150px" }} object src={`https://www.togedoortestgo.site/graphql/${props.serviceImage}`} alt='job' />
            </Media>
            <Media body className='pl-5 pr-5 align-self-center'>
              Project title:  {props.JobTitle}
              <ul className='m-0 mt-2 p-0'>
                <li className='mr-3'>
                  <FontAwesomeIcon className='pr-1 text-primary' icon={faNewspaper} />${props.Salary}
                </li>
                <li className=' mt-2'>
                  <FontAwesomeIcon className='pr-1 text-primary' icon={faMapMarker} />
                  {props.createdAt}
                </li>
              </ul>
            </Media>
          </Media>
        </div>
        <div className={` ${secondColmn} `}>
          <ListGroup className={`d-block ${textPositon}`} >
            <ListGroupItem className=' p-2 d-inline-block'>
              <Link to={{
                pathname: `/job/${props._id}`,
              }}>
                <Button size='sm' color='primary' className='outline-hover--effect'>
                  view this project
                </Button>
              </Link>
            </ListGroupItem>
            <ListGroupItem className=' p-2 d-inline-block'>
              <li>
                <FontAwesomeIcon className="font-14" icon={faTrashAlt}
                  onClick={() => { submit(props._id) }}
                />
              </li>
            </ListGroupItem>
          </ListGroup>

        </div>
      </Row>
    </Card>
  );
};

export default JobItemApplied;
