import React, { useState, useEffect } from "react";
import { Row, Col } from 'reactstrap';
import store from '../../../store'
import axios from "axios";
// import PaginationItems from './PaginationItems';
// Images
import JobItemApplied from 'src/components/JobItemApplied';
import jobimg from 'src/assets/images/JobItem/logo.png';



const AppliedJob = (props) => {

    const setAppliesProjects = (element) => {
        props.userAppliedProject(element)
    }

    return (
        <>
            {props.user.appliedProjects ? (
                <Row>
                    <Col md={12}>
                        <h6 className='border p-4 bg-primary text-white'> Applied projects</h6>
                        {props.user.id &&
                            props.user.appliedProjects
                                .map((service, i) => <JobItemApplied key={i} {...service}
                                    userAppliedProject={setAppliesProjects}
                                    firstColumn='col-md-10 col-sm-12 col-xs-12'
                                    secondColumn='col-md-2 col-sm-12 col-xs-12'
                                    textPosition='text-left text-md-right'
                                />)}
                    </Col>
                </Row>
            ) : <div>Loading...</div>

            }

        </>

    )

}


export default AppliedJob;
